import { useState, useRef, useEffect } from 'react';
import React from 'react';

// MUI Includes
import Alert from '@mui/material/Alert';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';

// MUI Icon Includes
import EmojiObjectsIcon from '@mui/icons-material/EmojiObjects';
import MusicNoteIcon from '@mui/icons-material/MusicNote';

// Local Includes
import EffectsList from './EffectsList';
import Header from './Header';
import SettingsDialog from './SettingsDialog';


const Controller = () => {

  const [connected, setConnected] = useState(false);
  const [settingsOpen, setSettingsOpen] = useState(false);
  const [patterns, setPatterns] = useState([]);
  const [patOpen, setPatOpen] = useState(true);
  const [animations, setAnimations] = useState([]);
  const [aniOpen, setAniOpen] = useState(true);
  const [brightness, setBrightness] = useState(0);
  const [volume, setVolume] = useState(0);
  const [bleError, setBleError] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');

  let patternsRef = useRef(patterns);
  let animationsRef = useRef(animations);
  let batDevice = useRef(undefined);
  let batCharacteristic = useRef(undefined);
  let BatState = useRef('connect');
  let numPatterns = useRef(0);
  let numAnimations = useRef(0);
  let currentPattern = useRef(0);
  let currentAnimation = useRef(0);
  let lastPlayedPattern = useRef(-1);
  
  // Update pattern and animation refs when they change so the
  // handleMessageReceived callback has the most recent info
  useEffect(() => {
    patternsRef.current = patterns;
    animationsRef.current = animations;
  }, [patterns, animations])

  
  const onClickConnect = () => {
    // Reset BLE Error if there was one
    setBleError(false);

    // Try to find the BAT Tree device
    let filters = [];
    filters.push({namePrefix: "BAT Tree"});
    let optionalServices = ['0000ffe0-0000-1000-8000-00805f9b34fb'];
    console.log(optionalServices);
    let options = {};
    options.filters = filters;
    options.optionalServices = optionalServices;
    console.log('Requesting Bluetooth Device...');

    // Request the device with the options
    navigator.bluetooth.requestDevice(options)

    // If success, connect to GATT server
    .then(device => {
      console.log('Connecting to GATT Server...');
      console.log(device);
      batDevice.current = device;
      return batDevice.current.gatt.connect();
    })

    // Grab the primary service for the BLE controller
    .then(server => {
      console.log('Getting Primary Service...');
      return server.getPrimaryService(0xFFE0);
    })

    // Connect to the characteristic
    .then(service => {
      console.log('Getting Custom Characteristic...');
      return service.getCharacteristic(0xFFE1);
    })

    // Register callback when connected to characteristic
    .then(characteristic => {
      console.log('We got all the way to the end!');
      batCharacteristic.current = characteristic;
      batCharacteristic.current.addEventListener('characteristicvaluechanged', handleMessageReceived);
      batCharacteristic.current.startNotifications();

      //  Update the state so connect goes away
      setConnected(true);
    })

    // Once successfully connected, start queries
    .then(() => {
      BatState.current = 'queryNumPat';
      batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('NumPat\n'));
    })

    // When something goes wrong in the promise chain
    .catch(error => {
      console.log(error);
      // Show the error string to the user so they can try
      // to do something about it.
      setErrorMessage(error.message);
      setBleError(true);
    });

  }



  
  const handleMessageReceived = (event) => {
    let respStr = String.fromCharCode.apply(null, new Uint8Array(event.target.value.buffer));
    if (respStr === "EOE") {
      console.log("EOE received");
      // When we receive EOE, we know the current effect has finished playing.
      // If it was a pattern, it will repeat.
      // If it was an animation, it will resume the last pattern
      // Therefore, we can set the active boolean of the last played pattern to true
      // First, clear all selected icons
      let pats = [...patternsRef.current];
      let anis = [...animationsRef.current];

      pats.forEach((pattern) => { pattern.active = false; })
      anis.forEach((animation) => {animation.active = false; })

      // Enable the selected icon
      if (lastPlayedPattern.current !== -1) {
        pats[lastPlayedPattern.current].active = true;
      }
      
      setPatterns(pats);
      setAnimations(anis);

      return;
    }

    if (BatState.current === 'queryNumPat') {
      console.log(respStr);
      numPatterns.current = parseInt(respStr);
      console.log('The number of patterns is ' + numPatterns.current);

      // Now that we have the number of patterns, let's get them
      if (numPatterns.current === 0) {
        console.log('Uh... I guess I need to handle this. But for now, it will break me.');
        return;
      }

      // Send the query for the first pattern to start the fun
      batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('GetPat 0\n'));
      currentPattern.current = 0;
      BatState.current = 'gatherPatterns';
    }

    else if (BatState.current === 'gatherPatterns') {

      console.log('Pattern ' + currentPattern.current + ': ' + respStr);
      currentPattern.current++;

      const newPattern = {name: respStr, active: false};

      setPatterns((prevState) => [...prevState, newPattern]);

      if (numPatterns.current > currentPattern.current) {
        let queryString = 'GetPat ' + currentPattern.current + '\n';
        batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my patterns');
        BatState.current = 'queryNumAni';
        batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('NumAni\n'));
      }
    }

    else if (BatState.current === 'queryNumAni') {
      numAnimations.current = parseInt(respStr);

      if (numAnimations.current === 0) {
        console.log('Same blah blah broken blah blah');
        return;
      }

      // Send the query for the first animation to start the fun
      batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('GetAni 0\n'));
      BatState.current = 'gatherAnimations';
    }

    else if (BatState.current === 'gatherAnimations') {
      console.log('Animation ' + currentAnimation.current + ': ' + respStr);
      currentAnimation.current++;

      setAnimations((prevAnis) => [...prevAnis, {name: respStr, active: false}]);
      console.log(animations);

      if (numAnimations.current > currentAnimation.current) {
        let queryString = 'GetAni ' + currentAnimation.current + '\n';
        batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(queryString));
      }
      else {
        console.log('I have received all of my animations?');
        console.log(animations);

        batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('GetBright\n'));
        BatState.current = 'queryBright';
      }
    }

    else if (BatState.current === 'queryBright') {
      console.log(respStr);
      setBrightness((parseInt(respStr)+1)/5*100);

      BatState.current = 'queryVol';
      batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode('GetVol\n'));
    }

    else if (BatState.current === 'queryVol') {
      const volume = (parseInt(respStr)+1)/8*100;
      console.log(volume);
      setVolume(volume);
      
      BatState.current = 'readyToRock';
    }

    else {
      console.log(`Response: ${respStr}`);
    }

  }

  const setPattern = (patNum) => {
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`SetPat ${patNum}\n`));

    // First, clear all selected icons
    let pats = [...patternsRef.current];
    let anis = [...animationsRef.current];
    pats.forEach((pattern) => { pattern.active = false; })
    anis.forEach((animation) => {animation.active = false; })

    // Enable the selected icon
    pats[patNum].active = true;

    setPatterns(pats);
    setAnimations(anis);
    
    lastPlayedPattern.current = patNum;
  }

  const setAnimation = (aniNum) => {
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`SetAni ${aniNum}\n`));

     // First, clear all selected icons
     let pats = [...patterns];
     let anis = [...animations];
     pats.forEach((pattern) => { pattern.active = false; })
     anis.forEach((animation) => {animation.active = false; })
 
     // Enable the selected icon
     anis[aniNum].active = true;
 
     setPatterns(pats);
     setAnimations(anis);
  }

  const handleIncBright = () => {
    BatState.current = 'queryBright';
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`IncBright\n`));
  }

  const handleDecBright = () => {
    BatState.current = 'queryBright';
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`DecBright\n`));
  }

  const handleIncVol = () => {
    BatState.current = 'queryVol';
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`IncVol\n`));
  }

  const handleDecVol = () => {
    BatState.current = 'queryVol';
    batCharacteristic.current.writeValueWithoutResponse(new TextEncoder().encode(`DecVol\n`));
  }



  return (
    <Box sx={{flexGrow: 1, pb: '20px'}}>
      <CssBaseline />
      <Header openSettings={() => setSettingsOpen(true)} /> 
      <Container maxWidth='sm' sx={{display: 'flex', flexDirection:'column', alignItems: 'center', justifyContent:'vertical'}}>
        {/* Display the Connect button only when there 
            is not a valid connection to a BAT Tree */}
        {!connected && 
          <Box 
            sx={{
              display: 'flex',
              width: '100%',
              alignItems: 'center',
              justifyContent: 'center',
              bgcolor: 'background.default',
              color: 'text.primary',
              borderRadius: 1,
              p: 3,
            }}>
            <Button onClick={onClickConnect}>Connect</Button>  
          </Box>
        }
        {/* Display all Patterns */}
        <EffectsList 
          name='Patterns' 
          open={patOpen} 
          effects={patterns} 
          handleClick={() => setPatOpen(!patOpen)}
          setEffect={setPattern} 
          icon={<EmojiObjectsIcon />} 
        />
        {/* Display all Animations */}
        <EffectsList 
          name='Animations' 
          open={aniOpen} 
          effects={animations}
          handleClick={() => setAniOpen(!aniOpen)}
          setEffect={setAnimation} 
          icon={<MusicNoteIcon />} 
        />
      </Container>

      {/* Adjust brightness and volume with a modal window */}
      <SettingsDialog 
        open={settingsOpen} 
        onClose={() => setSettingsOpen(false)} 
        connected={connected}
        brightPct={brightness} 
        volPct={volume} 
        incBrightness={handleIncBright}
        decBrightness={handleDecBright}
        incVolume={handleIncVol}
        decVolume={handleDecVol}
      />
      
      {/* Display an Alert if there's a problem connecting to BLE */}
      {bleError &&
        <Alert 
          variant='outlined' 
          severity='error'
          sx={{
            m: '20px'
          }}
        >
          {errorMessage}
        </Alert>
      }
    </Box>
  );
}

export default Controller;