import React from 'react';
import Icon from '@mui/material/Icon';

import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Collapse from '@mui/material/Collapse';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import CheckCircleIcon from '@mui/icons-material/CheckCircle';


const EffectsList = (props) => {  
  return (
    props.effects.length > 0 &&
    <List
      sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}
      component="nav"
      aria-labelledby="nested-list-subheader"
    >
      <ListItemButton onClick={props.handleClick}>
        <ListItemIcon>
          <Icon>{props.icon}</Icon>
        </ListItemIcon>
        <ListItemText primary={props.name} />
        {props.open ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={props.open} timeout="auto" unmountOnExit>
        {props.effects.map((pattern, idx) => (
          <List key={pattern.name} component="div" disablePadding>
            <ListItemButton sx={{ pl: 4 }} onClick={() => {props.setEffect(idx)}}>
              <ListItemText primary={pattern.name} />
              {pattern.active && <CheckCircleIcon color='inherit' />}
            </ListItemButton>
          </List>
        ))}
      </Collapse>
    </List>
  )
};

export default EffectsList;